@font-face {
  font-family: "Sono";
  src: url("../fonts/sono/Sono-VariableFont_MONO,wght.ttf");
}
@font-face {
  font-family: "Coiny";
  src: url("../fonts/coiny/Coiny-Regular.ttf");
}
@font-face {
  font-family: "Softwareprogrammierung";
  src: url("../fonts/Softwareprogrammierung/Softwareprogrammierung.ttf");
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #292524;
  color: #fafaf9;
  padding: 48px 64px 0px;
  font-family: "Sono", sans-serif;
}
.btn-newFact {
  font-family: "Coiny", sans-serif;
}

/* MAIN LAYOUT */

.hidden {
  display: none !important;
}

#root {
  max-width: 1000px;
  margin: 0 auto;
}

section {
  overflow: scroll;
  padding-bottom: 40px;
}

.header {
  margin-bottom: 40px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.fact-form {
  margin-bottom: 40px;
  padding: 16px 40px;
  background-color: #44403c;
  border-radius: 10px;
  text-align: center;
  font-family: "Sono", sans-serif;
  font-size: 20px;
  display: flex;
  align-items: center;
  gap: 16px;
}

.fact-form input,
.fact-form select {
  background-color: #78716c;
  border: none;
  border-radius: 50px;
  padding: 14px;
  font-size: 14px;
  color: inherit;
  font-family: inherit;
}
.disputed {
  color: #ef4444;
  font-weight: 600;
  margin-right: 10px;
}

.fact-form input::placeholder {
  color: #a8a29e;
}
.fact-form span {
  font-weight: 600;
  font-size: 18px;
  margin-right: 18px;
}
.fact-form input:first-child {
  flex-grow: 1;
}

.main {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 24px;
  height: calc(100vh - 89px - 40px - 48px);
  /* background-color: red; */
}

/* ELEMENTS */

.logo {
  display: flex;
  align-items: center;
  gap: 16px;
}

h1 {
  font-size: 42px;
  font-family: "Coiny", sans-serif;
  text-transform: uppercase;
  line-height: 1;
  padding-top: 6px;
}

.fact {
  margin-bottom: 16px;
  padding: 16px 24px;
  font-size: 16px;
  line-height: 1.4;
  font-family: "Sono", sans-serif;
  background-color: #44403c;
  border-radius: 10px;
  letter-spacing: -1px;

  display: flex;
  align-items: center;
  gap: 12px;
}

.tag {
  text-transform: uppercase;
  font-size: 14px;
  font-family: "Coiny", Courier, monospace;
  padding-top: 3px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 50px;
  cursor: pointer;
}

.vote-buttons {
  margin-left: auto;
  flex-shrink: 0;
  display: flex;
  gap: 5px;
}

.vote-buttons button {
  background-color: #78716c;
  border: none;
  font-size: 14px;
  padding: 6px 12px;
  border-radius: 50px;
  color: inherit;
  font-weight: 600;
  cursor: pointer;
}

.vote-buttons button:hover {
  background-color: #292524;
}
.vote-buttons button:disabled {
  background-color: #44403c;
}

ul {
  list-style: none;
}

.btn {
  border: none;
  line-height: 1;
  text-transform: uppercase;
  border-radius: 50px;
  color: #fafaf9;
  font-size: 17px;
  padding: 16px 20px 13px;
  color: inherit;
  margin: 6px;
  transition: 0.3s;
  cursor: pointer;
}

.btn:hover {
  transform: scale(110%) rotate(2deg);
}

.btn-large {
  font-size: 20px;
  padding: 20px 32px 17px;
}

.btn-all-categories {
  margin-bottom: 16px;
  width: 100%;
}

.btn-category {
  width: 100%;
}

.logo img {
  width: 68px;
  height: 68px;
}

.source:link,
.source:visited {
  color: #a8a29e;
  text-decoration: none;
  transition: 0.3s;
}
.source:hover,
.source:active {
  color: #3b82f6;
}

.message {
  text-align: center;
  text-transform: uppercase;
  font-size: 32px;
  font-weight: 600;
  margin-top: 24px;
}

.all {
  background-image: linear-gradient(135deg, #3b82f6, #ef4444, #16a34a, #eab308);
}
