@media (max-width: 1100px) {
  body {
    background-color: #292524;
    color: #fafaf9;
    /* padding: 24px; */
  }
  .fact {
    /* background-color: #ef4444; */
    flex-direction: column;
    gap: 12px;
  }

  .fact .tag {
    margin-left: auto;
  }

  .fact-form {
    flex-direction: column;
    align-items: stretch;
  }

  .fact-form input,
  .fact-form select {
    width: auto;
  }
}

@media (max-width: 900px) {
  .main {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 900px) {
  .main {
    grid-template-columns: 1fr;
    height: auto;
  }
}

@media (max-width: 600px) {
  #root {
    padding: 24px;
  }

  .header {
    flex-direction: column;
    gap: 16px;
  }

  .fact-form {
    padding: 16px;
  }

  .fact {
    padding: 12px 16px;
  }
}
